@import url('https://fonts.googleapis.com/css2?family=Gill+Sans:wght@400;700&display=swap');
body, html, #root, .App {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background: linear-gradient(135deg, #7b2ff2 0%, #f357a8 100%);
  position: relative;
  overflow-x: hidden;
}
.stars {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 0;
}
.container {
  max-width: 500px;
  margin: 60px auto 0 auto;
  background: rgba(60, 0, 90, 0.85);
  border-radius: 24px;
  box-shadow: 0 8px 32px 0 #7b2ff299;
  padding: 32px 24px 24px 24px;
  position: relative;
  z-index: 1;
}
h1 {
  color: #fff;
  text-align: center;
  margin-bottom: 16px;
  letter-spacing: 2px;
  text-shadow: 0 0 8px #e0b3ff, 0 0 16px #7b2ff2;
}
.chat-box {
  background: rgba(255,255,255,0.08);
  border-radius: 16px;
  min-height: 300px;
  max-height: 350px;
  overflow-y: auto;
  padding: 16px;
  margin-bottom: 18px;
  box-shadow: 0 2px 8px #f357a822;
}
.message {
  margin-bottom: 14px;
  display: flex;
  flex-direction: column;
}
.user {
  align-self: flex-end;
  background: linear-gradient(90deg, #f357a8 0%, #7b2ff2 100%);
  color: #fff;
  padding: 10px 18px;
  border-radius: 18px 18px 4px 18px;
  font-weight: 600;
  box-shadow: 0 2px 8px #f357a822;
}
.bot {
  align-self: flex-start;
  background: linear-gradient(90deg, #7b2ff2 0%, #f357a8 100%);
  color: #fff;
  padding: 10px 18px;
  border-radius: 18px 18px 18px 4px;
  font-weight: 600;
  box-shadow: 0 2px 8px #7b2ff222;
}
.chat-form {
  display: flex;
  gap: 10px;
}
input[type="text"] {
  flex: 1;
  padding: 12px 16px;
  border-radius: 16px;
  border: none;
  font-size: 1rem;
  background: #f3e6ff;
  color: #4b006e;
  outline: none;
  box-shadow: 0 2px 8px #7b2ff222;
}
button {
  background: linear-gradient(90deg, #f357a8 0%, #7b2ff2 100%);
  color: #fff;
  border: none;
  border-radius: 16px;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 8px #f357a822;
  transition: background 0.2s;
}
button:hover {
  background: linear-gradient(90deg, #7b2ff2 0%, #f357a8 100%);
}
.footer {
  text-align: center;
  color: #fff;
  margin-top: 18px;
  font-size: 0.95rem;
  opacity: 0.7;
}
